ul {
  list-style: none;
  padding: 0;
}

.hidden {
  display: none;
}

.container {
  width: 320px;
  margin: 0 auto;
  background-color: var(--color-white);
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    width: 1024px;
  }
}
